// @flow strict
import React, { useState, useEffect } from 'react';
import styles from './Among.module.scss';
import styled, { keyframes } from 'styled-components';

let fly = (sv, ev) => keyframes`
  0% {
      left: -30%;
      top: ${sv}%;
      transform: rotate(0deg);
    }
    100% {
      left: 130%;
      top: ${ev}%;
      transform: rotate(720deg);
    }
`;
const Imposter = styled.img`
  position: absolute;
  width: 200px;
  animation: ${({ sv, ev }) => fly(sv, ev)} 6s linear;
  opacity: 30%;
`;
const Impo = ({ SV, EV }) => {
  const [unMount, useUnMount] = useState(true);

  useEffect(() => {
    console.log('');
    setTimeout(() => {
      console.log('');
      useUnMount(false);
    }, 5000);
  }, []);

  if (unMount) {
    return (<>
      <Imposter sv={SV} ev={EV} src={'/media/black_among.png'}/>
    </>);
  } else {
    return <></>;
  }

};
const Among = () => {
  const [impos, useImpos] = useState([]);

  const work = () => {
    const SV = Math.floor(Math.random() * 100);
    const EV = Math.floor(Math.random() * 100);
    useImpos(impos.concat(React.cloneElement(<Impo SV={SV} EV={EV}/>)));
  };

  return (
    <>
      <div className={styles.makeImpo} onClick={work}><span>EMERGENCY</span></div>
      <div className={styles.backgroundAmongWrap}>
        {impos.map((i, key) => {
          console.log(i.props);
          return i;
        })}
      </div>
    </>
  );
};

export default Among;
